<template>
<div class="page subpage" id="search">
    <!-- <h1>搜索</h1> -->
    <div class="top">
        <input class="topsearch" type="text" placeholder="搜索商品，共20166款好商品">
        <router-link to="/home" class="cancel">取消</router-link>       
    </div>

    
    <p class="hotsearch">热门搜索</p>
    <div class="menu">
      <router-link to="/home/search/result/life" class="menu-item" :class="{active:index === selectedIndex}" 
        v-for="(item, index) in navData" :key="item.id"
        @click="changeIndex(index)">
        {{item.name}}
      </router-link>
    </div>
    
</div>
</template>
<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      selectedIndex: 0
    };
  },
  computed: {
    ...mapState({
      navData: state => state.home.navData
    })
  },
  methods: {
    getHomeInitData() {
      this.$store.dispatch("home/requestHomeNavData");
    },
    changeIndex(index) {
      this.selectedIndex = index;
    },
    created() {
      this.getHomeInitData();
    }
  }
};
</script>

